@import "component/dcm-variables";
@import "component/dcm-mixins";

// Note: !important used to override other css file.

.path-frontpage {
  // Main menu
  .navbar.container {
    background-color: $blue-color;
    // Logo
    .navbar-header {
      .logo {
        img {
          height: 200px;
          @media (min-width: 320px) and (max-width: 960px) {
            height: 105px;
          }
        }
      }
    }
  }
  .main-container,
  .container {
    max-width: inherit;
    min-height: inherit;
    padding: 0 !important;
    width: 100%;
    .page-header {
      display: none;
    }
  }
  // Banner
  .banner {
    background-image: linear-gradient(180deg, rgba(43, 122, 183, .8), rgba(43, 123, 186, .8)), url(../images/camp-photo-2017-without-logo.jpg);
    background-position: 0 0, 50% 50%;
    background-repeat: repeat, no-repeat;
    background-size: auto, cover;
    display: block;
    padding-bottom: 8%;
    padding-top: 8%;
    position: relative;
    width: 100%;
    .region-banner {
      margin: 0 auto;
      max-width: $page-width;
      .slogan {
        position: relative;
        text-align: center;
        .mini-title {
          color: $white-color;
        }
        h1 {
          color: $white-color;
          font-family: $raleway-extrabold;
          font-size: 90px;
          line-height: 100px;
          margin-bottom: 10px;
          margin-top: 10px;
          @media (min-width: 320px) and (max-width: 767px) {
            font-size: 60px;
            line-height: 70px;
          }
        }
        .underline {
          background-color: $white-color;
        }
        .cursive-text {
          color: $white-color;
          font-family: Georgia;
          font-size: 24px;
          font-style: italic;
          line-height: 32px;
          margin-bottom: 10px;
          @media (min-width: 320px) and (max-width: 767px) {
            font-size: 21px;
            line-height: 25px;
          }
        }
      }
    }
  }
  // Facebook and Twitter
  .social {
    background-color: $gray-color5;
    float: left;
    padding: 40px 0 80px;
    position: relative;
    width: 100%;
    &:before {
      @include cross;
    }
    .region-social {
      margin: 0 auto;
      max-width: $page-width;
    }
    .fb-block {
      background-color: $gray-color5;
      height: 600px;
      overflow-y: auto;
      padding: 15px;
      width: 30%;
    }
    .twtr-block {
      background-color: $gray-color5;
      height: 600px;
      overflow-y: auto;
      padding: 15px;
      width: 70%;
    }
  }
  // Footer copyright
  .footer-copyright {
    height: auto !important;
  }
}

.mini-title {
  color: $base-color;
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 3.3px;
  line-height: 10px;
  position: relative;
  text-transform: uppercase;
  z-index: 1;
}
.underline {
  background-color: $base-color;
  border-radius: 3px;
  display: inline-block;
  height: 5px;
  margin-bottom: 10px;
  margin-top: 10px;
  position: relative;
  width: 95px;
  z-index: 1;
}

// Sponsers block
.block-views-blocksponsors-platinum-sponsors,
.block-views-blocksponsors-silver-sponsor-slideshow,
.block-views-blocksponsors-partners-slideshow,
.block-views-blocksponsors-gold-sponsors {
  background-color: $white-color;
  clear: both;
  padding: 30px 0 60px;
  position: relative;
  &:before {
    @include cross;
    top: -40px;
  }
  .block-title {
    margin-bottom: 22px;
    padding-bottom: 18px;
    position: relative;
    text-align: center;
    font-size: 24px;
    // &:after {
    //   background-color: $blue-color;
    //   bottom: 0;
    //   content: "";
    //   height: 3px;
    //   left: 50%;
    //   position: absolute;
    //   -webkit-transform: translateX(-50%);
    //   transform: translateX(-50%);
    //   width: 60px;
    // }
  }
  .view-sponsors {
    margin: 0 auto;
    max-width: $page-width;
    position: relative;
    text-align: center;
    .views-row {
      display: inline-block;
      margin: 0 20px 40px 0;
      text-align: center;
      vertical-align: middle;
      width: 22.5%;
      &:nth-child(4n) {
        margin-right: 0;
      }
      img {
        margin: 0 auto;
        -webkit-transition: all 300ms ease;
        transition: all 300ms ease;
        &:hover {
          -webkit-transform: scale(1.1);
          -ms-transform: scale(1.1);
          transform: scale(1.1);
        }
      }
      .views-field-title {
        display: none;
      }
    }
  }
}

.block-views-blocksponsors-platinum-sponsors {
  .block-title {
    background-image: url(../images/platinum_ribbon_01.png);
    background-repeat: no-repeat;
    background-position: center 0;
        color: $white-color;
  }
}

.block-views-blocksponsors-gold-sponsors {
  .block-title {
    background-image: url(../images/gold_ribbon_01.png);
    background-repeat: no-repeat;
    background-position: center 0;
        color: $base-color;
        line-height: 34px;
  }
}

.block-views-blocksponsors-silver-sponsor-slideshow {
  .block-title {
    background-image: url(../images/silver_ribbon_01.png);
    background-repeat: no-repeat;
    background-position: center 0;
        color: $base-color;
        line-height: 34px;
  }
}

.block-views-blocksponsors-partners-slideshow {
  .block-title {
    background-image: url(../images/red_ribbon_01.png);
    background-repeat: no-repeat;
    background-position: center 0;
        color: $white-color;
        line-height: 34px;
  }
}

// Connect us block
#block-connectwithus {
  text-align: center;

  .social-icon {
    display: inline-block;
    margin: 0 5px;

    a {
      font-size: 0;

      &:before {
        font-size: 20px;
        @include base-fa;
      }
    }

    &.icon-twitter {
      a {
        &:before {
          content: "\f099";
        }
      }
    }

    &.icon-facebook {
      a {
        &:before {
          content: "\f09a";
        }
      }
    }

    &.icon-googleplus {
      a {
        &:before {
          content: "\f0d5";
        }
      }
    }

    &.icon-drupalorg {
      a {
        &:before {
          content: "\f1a9";
        }
      }
    }
  }
}

// Recent News
.path-frontpage {
  #middle-content {
    background-color: $blue-color1;
    padding: 50px 0 50px;
    position: relative;
    &:before {
      @include cross;
    }
    .region {
      margin: 0 auto;
      max-width: $page-width;
      min-width: 320px;
      width: 100%;
      .drupal-recent-news {
        .block-title {
          color: $blue-color;
          font-family: $raleway-extrabold;
          font-size: 35px;
          margin-bottom: 40px;
        }
        .view-drupal-camp-news {
          margin: 0 auto;
          max-width: $page-width;
          position: relative;
          .views-row {
            border-bottom: 0;
            float: left;
            margin: 0 0 40px;
            padding: 0 0 5px 15px;
            width: 100%;
            .views-field {
              margin: 5px 0 0;
              .homepage-new {
                .date-mnth {
                  .inner {
                    background-color: $blue-color;
                    float: left;
                    height: 78px;
                    position: relative;
                    text-align: center;
                    width: 78px;
                    &:before {
                      background-color: $white-color;
                      content: "";
                      height: 5px;
                      left: 10px;
                      position: absolute;
                      top: 0;
                      width: 5px;
                    }
                    &:after {
                      background-color: $white-color;
                      content: "";
                      height: 5px;
                      position: absolute;
                      right: 10px;
                      top: 0;
                      width: 5px;
                    }
                    h1 {
                      color: $white-color;
                      font-family: $raleway-extrabold;
                      font-size: 32px;
                      margin-bottom: 0;
                      padding-bottom: 5px !important;
                      padding-top: 5px !important;
                    }
                    h4 {
                      color: $white-color;
                      font-family: $raleway-extrabold;
                      font-size: 18px;
                      text-transform: uppercase !important;
                    }
                  }
                }
                .data-wrapper {
                  padding-right: 25px;
                  @media screen and (max-width: 991px) {
                    padding-right: 0;
                  }
                  .news-title {
                    a {
                      color: $blue-color;
                      display: inline-block;
                      font-size: 27px;
                      font-weight: bold;
                      line-height: 32px;
                      margin-bottom: 10px;
                      margin-top: 0;
                      text-decoration: initial;
                    }
                  }
                }
              }
            }

            .views-more-link {
              margin: 10px 12px;
              text-align: right;

              a {
                border-left: 2px solid $link-color;
                padding-left: 5px;
                &:after {
                  content: " >>";
                }
              }
            }
          }
        }
      }
      .gold-sponsors {
        text-align: center;

        .form-group {
          .view-content {
            .views-row {
              display: table;
              margin: 0 auto 15px;
              @media screen and (max-width: 991px) {
                display: inline-block;
                margin: 10px;
              }
            }
          }
        }
      }
    }
  }
}
// End Recent News

